@font-face {
  font-family: gilroy;
  src: url("../fonts/Gilroy-Light.woff2") format("woff2"), url("../fonts/Gilroy-Light.woff") format("woff");
  /* url('../fonts/Gilroy-Light.ttf') format('truetype'); */
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: gilroy;
  src: url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff");
  /* url('../fonts/Gilroy-Medium.ttf') format('truetype'); */
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: gilroy;
  src: url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff");
  /* url('./Gilroy-Bold.ttf') format('truetype'); */
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: franklin;
  src: url("../fonts/Franklin-Gothic-Demi-Cond.woff2") format("woff2"), url("../fonts/Franklin-Gothic-Demi-Cond.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --color-primary: #002760;
  --color-secondary: rgb(19, 50, 62);
  --color-contrast: rgba(175, 150, 135, 1);
  --color-brown: #cb9e88;
  --color-dark: #1b1919;
  --color-light: #fff2e9;
  --color-salmon: #e9856f;
  --color-cornflowerblue: #6f85e9;
  --bg-color-primary: rgba(240, 240, 240, 0.3);
  --bg-color-secondary: #fff2e9;
  --main-font-bold: normal normal 700 1rem "gilroy", sans-serif;
  --main-font-regular: normal normal 400 1rem "gilroy", sans-serif;
  --main-font-light: normal normal 300 1rem "gilroy", sans-serif;
  --secondary-font-bold: normal normal 700 1rem "franklin", sans-serif;
  --main-bg-gradient: linear-gradient(90deg, rgba(135, 150, 175, 1) 0%, rgba(255, 255, 255, 0) 100%);
  --header-height: 4rem;
  --text-shadow-primary: 1px 1px 2px rgba(0, 0, 0, 0.5) ;
}
html,
body {
  box-sizing: border-box;
  margin: 0;
  font: var(--main-font-regular);
  /* background: var(--main-bg-gradient) no-repeat; */
  height: 100%;
  color: var(--color-primary);
  background-color: var(--bg-color-primary);
}

.divider {
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
}
.content {
  /* background-color: var(--main-bg-color); */
  margin: auto;
  width: 100%;
  box-sizing: border-box;
}
.title-primary {
  line-height: 1.2;
  margin: 0;
  color: var(--color-secondary);
  font: var(--main-font-light);
  font-size: 3.75rem;
}

.title-secondary {
  line-height: 1.2;
  margin: 0;
  color: var(--color-contrast);
  font: var(--main-font-bold);
  font-size: 1rem;
}
.header {
  align-content: center;
  align-items: center;
  background-color: #fbfbfb;
  display: flex;
  flex-direction: row;
  height: var(--header-height);
  position: sticky;
  top: 0;
  box-shadow: 0 -4px 8px black;
  z-index: 9;
}

.header__header-inner {
  padding: 0 2.6em;
}

.header-inner {
  display: flex;
  justify-content: space-between;
  width: 100vw;
}

.header__menu {
  align-self: center;
}

.menu__link {
  color: var(--color-primary);
  margin-right: 1.7em;
  text-decoration: none;
  transition: color 0.2s;
}

.menu__link_active {
  color: var(--color-contrast);
}

.menu__link:hover {
  color: var(--color-contrast);
}

.header__logo {
  transform: translateX(-9.3em);
}

.logo {
  background: no-repeat center/contain url(../img/logo.svg);
  height: 2.6875em;
  width: 8.75em;
}

/*
.header__burger-menu {
  visibility: hidden;
} */
@media (min-width: 1024px) and (max-width: 1439px) {
  .header__menu {
    margin-right: 3em;
  }
  .menu__link {
    font-size: 0.8em;
  }
  .logo {
    height: 1.5em;
  }
  .header {
    height: 4em;
  }
}
@media (min-width: 320px) and (max-width: 1023px) {
  .header {
    height: 4em;
  }
  .header__header-inner {
    padding: 0 2em;
  }
  .header-inner {
    align-items: center;
  }
  .menu {
    display: none;
  }
  .header__logo {
    transform: translateX(0);
  }
  .logo {
    height: 1.4em;
    width: 4.6em;
  }
}
.burger-menu {
  /* position: relative;
  display: inline-block; */
  /* display: none; */
  visibility: hidden;
}

.burger-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 2.5rem;
  height: 2rem;
}

.burger-menu__line {
  background: var(--color-primary);
  height: 0.1rem;
  /* margin: .1rem; */
  width: 100%;
  transition: all 0.3s ease;
}

.burger-btn.active .burger-menu__line:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
}

.burger-btn.active .burger-menu__line:nth-child(2) {
  opacity: 0;
}

.burger-btn.active .burger-menu__line:nth-child(3) {
  transform: translateY(-5px) rotate(-45deg);
}

.burger-menu__layer {
  color: var(--color-primary);
  position: absolute;
  top: var(--header-height);
  left: 0;
  right: 0;
  /* bottom: 0; */
  background-color: white;
  /* border: 1px solid #ddd; */
  /* width: 200px; */
  overflow: hidden;
  transition: all 0.3s ease;
  height: 0;
  z-index: 2;
  touch-action: none;
  box-sizing: border-box;
}

.burger-menu__layer.active {
  height: 100vh;
}

.menu-item {
  padding: 12px 15px;
  color: var(--color-primary);
  text-decoration: none;
  display: block;
  transition: background-color 0.2s;
}

.menu-item:hover {
  background-color: #f5f5f5;
}

.menu-item:not(:last-child) {
  border-bottom: 1px solid #eee;
}

@media (max-width: 1023px) {
  .burger-menu {
    visibility: visible;
  }
}
.home-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding-top: 2.8rem;
  padding-bottom: 2.8rem;
  background-image: var(--main-bg-gradient), url(../img/home-screen/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: calc(100vh - var(--header-height));
}

.home-page__main-header {
  padding: 0 2rem;
  width: 100%;
  box-sizing: border-box;
}

.main-header__title {
  margin: 0;
  color: var(--color-secondary);
  font: var(--main-font-light);
}

.main-title {
  font-size: 3.75rem;
  line-height: 1.2;
}

.main-header__main-subtitle {
  margin: 0;
  color: var(--color-secondary);
  font: var(--main-font-light);
}

.main-subtitle {
  font-size: 1.56rem;
}

.main-content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 2rem;
  color: var(--color-secondary);
  font: var(--main-font-light);
  font-size: 1.5rem;
}

.home-page__content-description-start,
.home-page__content-description-end {
  width: 50%;
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .main-title {
    font-size: 3rem;
  }
  .main-content-wrapper {
    font-size: 1.2rem;
  }
}
@media (max-width: 1023px) {
  .home-page {
    background-position: center;
  }
  .main-header {
    width: 22rem;
  }
  .main-title {
    font-size: 1.8rem;
  }
  .home-page__main-content-wrapper {
    font-size: 3.5vw;
  }
  .main-subtitle {
    font-size: 0.8rem;
  }
}
@media (max-width: 820px) {
  .main-content-wrapper {
    font-size: 2.5vw;
  }
}
.last-years {
  padding: 10rem 10rem;
  box-sizing: border-box;
  background-color: var(--bg-color-primary);
}

.last-years__description {
  box-sizing: border-box;
  text-align: center;
  font: var(--secondary-font-bold);
  font-size: 3.2rem;
  letter-spacing: 0.2rem;
  /* color: var(--color-contrast); */
  color: var(--color-primary);
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .last-years {
    padding: 5rem;
  }
  .last-years__description {
    font-size: 2.2rem;
  }
}
@media (max-width: 1023px) {
  .last-years {
    padding: 5vw;
  }
  .last-years__description {
    font-size: 2.2vw;
  }
}
.business-directions {
  padding: 2rem 2rem 4rem;
  box-sizing: border-box;
  background: var(--main-bg-gradient);
}

.business-directions__title {
  position: relative;
  z-index: 1;
}

.business-directions__list {
  list-style: "-";
  font: var(--main-font-light);
  color: var(--color-secondary);
  font-size: 2rem;
}

.business-directions__list li {
  padding-left: 1rem;
  line-height: 1.5;
}

.business-directions__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.business-directions__photos-wrapper {
  position: relative;
  width: 50rem;
  height: 25rem;
}

.business-directions__photo {
  height: 20rem;
  width: 20rem;
  position: absolute;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
  transition: all 0.2s ease-in-out;
  transform: skew(0deg, 10deg);
  border-radius: 0.2rem;
  background-size: cover;
}

.business-directions__photo:hover {
  z-index: 1;
}

.business-directions__photo_1 {
  bottom: 0;
  left: 0;
}

.business-directions__photo_2 {
  bottom: 2rem;
  left: 5rem;
}

.business-directions__photo_3 {
  bottom: 4rem;
  left: 10rem;
}

.business-directions__photo_4 {
  bottom: 6rem;
  left: 15rem;
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .business-directions__list {
    font-size: 1.8rem;
  }
  .business-directions__photo {
    height: 15rem;
    width: 15rem;
  }
}
@media (max-width: 1023px) {
  .business-directions__title {
    font-size: 5vw;
  }
  .business-directions__list {
    font-size: 3vw;
  }
  .business-directions__photo {
    height: 27vw;
    width: 27vw;
  }
}
@media (max-width: 790px) {
  .business-directions__title {
    font-size: 4vw;
  }
  .business-directions__list {
    padding-left: 2vw;
    font-size: 2.5vw;
    width: 50%;
  }
  .business-directions__photos-wrapper {
    width: 50%;
    height: 30vw;
  }
  .business-directions__photo {
    height: 20vw;
    width: 20vw;
  }
  .business-directions__photo_2 {
    bottom: 2vw;
    left: 5vw;
  }
  .business-directions__photo_3 {
    bottom: 4vw;
    left: 10vw;
  }
  .business-directions__photo_4 {
    bottom: 6vw;
    left: 15vw;
  }
}
.about-us {
  /* background-color: var(--bg-color-primary); */
  padding: 2rem;
  color: var(--color-contrast);
  font: var(--main-font-light);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  /* position: absolute; */
  top: var(--header-height);
  bottom: 0;
  overflow: auto;
  z-index: 0;
  margin: auto;
}

.about-us__title {
  color: var(--color-contrast);
}

.about-us__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  flex-grow: 1;
}

.about-us__director-info-wrapper {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.about-us__director-info {
  text-align: justify;
  grid-column-start: 2;
  grid-column-end: 3;
}

.about-us__divider {
  height: 5px;
  width: 80%;
  margin-bottom: 1rem;
}

.about-us__organization-title {
  font-size: 1.2rem;
}

.about-us__organizations-wrapper {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
  text-align: justify;
  font-size: 1.2rem;
}

@media (max-width: 1024px) {
  .about-us__director-info {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}
@media (max-width: 1023px) {
  .about-us {
    font-size: 2vw;
  }
  .about-us__director-info-wrapper {
    font-size: inherit;
  }
  .about-us__title {
    font-size: 7vw;
  }
  .about-us__organizations-wrapper {
    font-size: inherit;
  }
}
@media (max-width: 1023px) {
  .about-us__organization-title {
    font-size: 1.5vw;
  }
}
@media (max-width: 414px) {
  .about-us {
    padding: 5vw;
  }
  .about-us__director-info-wrapper {
    width: 100%;
  }
  .about-us__divider {
    width: 100%;
  }
  .about-us__organizations-wrapper {
    width: 100%;
    grid-template-columns: 1fr;
    gap: 5vw;
  }
}
.contacts {
  background-color: var(--bg-color-primary);
  padding: 2rem;
  color: var(--color-contrast);
  font: var(--main-font-light);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: absolute;
  top: var(--header-height);
  bottom: 0;
  left: 0;
  right: 0;
}

.contacts__title {
  color: var(--color-contrast);
  margin-bottom: 2rem;
}

.contacts__content-wrapper {
  margin: auto;
  text-align: justify;
}

.contacts__phone {
  font-size: 3rem;
  text-decoration: none;
  color: inherit;
  margin-bottom: 0.2rem;
}

.contacts__address {
  font-size: 1.2rem;
  margin-bottom: 3rem;
}

.contacts__mail {
  font-size: 1.2rem;
  text-decoration: none;
  color: inherit;
}

.contacts__company-data {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

@media (max-width: 414px) {
  .contacts {
    padding: 4vw;
  }
  .contacts__title {
    font-size: 10vw;
  }
  .contacts__phone {
    font-size: 10vw;
  }
  .contacts__address,
  .contacts__mail {
    font-size: 4vw;
  }
}
@charset "UTF-8";
.projects {
  background-color: var(--bg-color-primary);
  padding: 2rem;
  color: var(--color-contrast);
  font: var(--main-font-light);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: absolute;
  top: var(--header-height);
  bottom: 0;
  left: 0;
  right: 0;
}

.projects-title {
  font-size: 2rem;
  color: var(--color-contrast);
}

.projects__projects-wrapper {
  overflow: auto;
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-template-rows: repeat(10, min-content);
  /* grid-template-rows: 42rem 54rem 56rem; */
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  /* плотное заполнение */
}

.projects__project {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.projects__project img {
  width: inherit;
  height: inherit;
  object-fit: cover;
}

.projects__project-description-layer {
  font: var(--main-font-light);
  color: white;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.projects__project-description-wrapper {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.2) 99%, rgba(0, 0, 0, 0) 100%);
  padding: 1rem;
}

.projects__project-description-name {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  opacity: 1;
}

@media (max-width: 1024px) {
  .projects-title {
    font-size: 1.8vw;
  }
  .projects__project-description-name {
    font-size: 1.35vw;
  }
  .projects__project-description-address {
    font-size: 0.8vw;
  }
}
@media (max-width: 414px) {
  .projects {
    gap: 2vw;
    padding: 3vw;
  }
  .projects__projects-wrapper {
    grid-template-columns: 1fr;
    gap: 2vw;
  }
  .projects-title {
    font-size: 2.4vw;
  }
  .projects__project-description-name {
    font-size: 2.4vw;
    margin-bottom: 1vw;
  }
  .projects__project-description-address {
    font-size: 1.8vw;
  }
}